@import './mixins.scss';
@import './variables.scss';

.aboutMe {
    padding-top: 4rem;
    scroll-snap-align: start;
    height: 100vh;
    @include text_generic;
    @include flex_center;
    background-color: $gray;
    margin: auto;
    max-width: $width-80;
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-template-rows: 1fr 5fr 5fr;

    &_title {
        @include title_section;
    }

    &_figure {
        margin-top: 2rem;
        margin-right: 2rem;
        grid-row: 2/2;
        grid-column: 1/2;

        &_img {
            width: 200px;
        }

    }

    &_container {
        grid-row: 2/3;
        grid-column: 2/3;
        display: flex;
        align-items: center;
        line-height: 1.2rem;

        &_text {
            font-size: 1rem;
            font-weight: 100;
            letter-spacing: 1.5px;
            margin-top: 2rem;
            text-align: justify;
            color: white;
        }

    }

    &_skills {
        grid-row: 3/4;
        grid-column: 2/30;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
        height: fit-content;
        padding-bottom: 15rem;

        &_tech {
            grid-column: 1/2;
            border-top: $border-green;
            border-right: $border-green;
            padding-right: 1rem;
        }

        &_soft {
            grid-column: 2/3;
            padding-right: 1rem;
            border-top: $border-green;
            border-right: $border-green;
        }
    }


}